export default {
	data() {
		return {
			id:0,
			pageIndex:1,
			pageSize:20,
			pageTotal:1,
			commontList:[],
			nodata:false,
			nomore:false
		};
	},
	onLoad(options){
		this.id = options.goodsId
	},
	onShow(){
		if(getApp().globalData.isShowPic){
			getApp().globalData.isShowPic=false
		}else{
			this.getCommentList()
		}
		
	},
	onReachBottom(){
		if(this.pageIndex<this.pageTotal){
			this.pageIndex++
			this.getCommentList()
		}else{
			this.nomore=true
		}
	},
	onPullDownRefresh(){
		this.setParams()
		this.getCommentList()
		uni.stopPullDownRefresh()
	},
	methods:{
		getCommentList(){
			let self = this
			let params = {
				pageIndex:this.comIndex,
				pageSize:this.comSize,
				id:this.id
			}
			this.$acFrame.HttpService.prodComments(params).then(res=>{
				if(res.success){
					self.comTotal=res.data.pageTotal
					let list = res.data.rows
					if(list.length>0){
						list.filter(v=>{
							v.commentTime  = self.$acFrame.Util.formatTime(v.commentTime,'day')
							v.imgPathList.filter((img,i)=>{
								v.imgPathList[i] = self.setImg(img)
							})
						})
						self.commontList= self.commontList.concat(list)
					}else{
						self.nodata=true
					}
					uni.setNavigationBarTitle({
						title:'评论（'+list.length+'）'
					})
				}
			})
		},
		showBig(pind,cind){
			let info = this.commontList[pind];
			let imgList = info.imgPathList;
			
			imgList.filter(v=>{
				v=this.setImg(v)
			})
			this.$acFrame.Util.showBigPic(imgList[cind], imgList);
			getApp().globalData.isShowPic = true
		},
		setParams(){
			this.pageIndex=1
			this.nodata=false
			this.nomore=false
			this.commontList=[]
		},
		setImg(src,genderType){
			return  this.$acFrame.Util.setImgUrl(src,genderType);
		},
	}
}
